
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import { searchHot } from "@/api/api.js";
export default {
data() {
return {
hots: []
};
},

beforeCreate() {},

created() {
this._searchHot();
},

beforeMount() {},

mounted() {},

beforeUpdate() {},

updated() {},

beforeDestroy() {},

destroyed() {},

methods: {
_searchHot() {
searchHot().then(res => {
this.hots = res.result.hots;
});
}
},
computed: {},
components: {
XInput,
Group,
Icon
}
};



